*,
*::before,
*::after {
  box-sizing: border-box;
}

html {
  font-family: sans-serif;
  line-height: 1.15;
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
  -ms-overflow-style: scrollbar;
  -webkit-tap-highlight-color: transparent;
}

@-ms-viewport {
  width: device-width;
}

article, aside, dialog, figcaption, figure, footer, header, hgroup, main, nav, section {
  display: block;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
  font-size: 1rem;
  font-weight: 400;
  line-height: 1.5;
  color: #212529;
  text-align: left;
  background-color: #fff;
}

[tabindex="-1"]:focus {
  outline: none !important;
}

hr {
  box-sizing: content-box;
  height: 0;
  overflow: visible;
}

h1, h2, h3, h4, h5, h6 {
  margin-top: 0;
  margin-bottom: 0;
}

p {
  margin-top: 0;
  margin-bottom: 1rem;
}

abbr[title],
abbr[data-original-title] {
  text-decoration: underline;
  -webkit-text-decoration: underline dotted;
  text-decoration: underline dotted;
  cursor: help;
  border-bottom: 0;
}

address {
  margin-bottom: 1rem;
  font-style: normal;
  line-height: inherit;
}

ol,
ul,
dl {
  margin-top: 0;
  margin-bottom: 1rem;
}

ol ol,
ul ul,
ol ul,
ul ol {
  margin-bottom: 0;
}

dt {
  font-weight: 700;
}

dd {
  margin-bottom: .5rem;
  margin-left: 0;
}

blockquote {
  margin: 0 0 1rem;
}

dfn {
  font-style: italic;
}

b,
strong {
  font-weight: bold;
}

small {
  font-size: 80%;
}

sub,
sup {
  position: relative;
  font-size: 75%;
  line-height: 0;
  vertical-align: baseline;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

a {
  color: #007bff;
  text-decoration: none;
  background-color: transparent;
  -webkit-text-decoration-skip: objects;
}

a:hover {
  color: #0056b3;
  text-decoration: underline;
}

a:not([href]):not([tabindex]) {
  color: inherit;
  text-decoration: none;
}

a:not([href]):not([tabindex]):focus, a:not([href]):not([tabindex]):hover {
  color: inherit;
  text-decoration: none;
}

a:not([href]):not([tabindex]):focus {
  outline: 0;
}

pre,
code,
kbd,
samp {
  font-family: monospace, monospace;
  font-size: 1em;
}

pre {
  margin-top: 0;
  margin-bottom: 1rem;
  overflow: auto;
  -ms-overflow-style: scrollbar;
}

figure {
  margin: 0 0 1rem;
}

img {
  vertical-align: middle;
  border-style: none;
}

svg:not(:root) {
  overflow: hidden;
}

a,
area,
button,
[role="button"],
input:not([type="range"]),
label,
select,
summary,
textarea {
  -ms-touch-action: manipulation;
  touch-action: manipulation;
}

table {
  border-collapse: collapse;
}

caption {
  padding-top: 0.75rem;
  padding-bottom: 0.75rem;
  color: #868e96;
  text-align: left;
  caption-side: bottom;
}

th {
  text-align: inherit;
}

label {
  display: inline-block;
  margin-bottom: .5rem;
}

button {
  border-radius: 0;
}

button:focus {
  outline: 1px dotted;
  outline: 5px auto -webkit-focus-ring-color;
}

input,
button,
select,
optgroup,
textarea {
  margin: 0;
  font-family: inherit;
  font-size: inherit;
  line-height: inherit;
}

button,
input {
  overflow: visible;
}

button,
select {
  text-transform: none;
}

button,
html [type="button"],
[type="reset"],
[type="submit"] {
  -webkit-appearance: button;
}

button::-moz-focus-inner,
[type="button"]::-moz-focus-inner,
[type="reset"]::-moz-focus-inner,
[type="submit"]::-moz-focus-inner {
  padding: 0;
  border-style: none;
}

input[type="radio"],
input[type="checkbox"] {
  box-sizing: border-box;
  padding: 0;
}

input[type="date"],
input[type="time"],
input[type="datetime-local"],
input[type="month"] {
  -webkit-appearance: listbox;
}

textarea {
  overflow: auto;
  resize: vertical;
}

fieldset {
  min-width: 0;
  padding: 0;
  margin: 0;
  border: 0;
}

legend {
  display: block;
  width: 100%;
  max-width: 100%;
  padding: 0;
  margin-bottom: .5rem;
  font-size: 1.5rem;
  line-height: inherit;
  color: inherit;
  white-space: normal;
}

progress {
  vertical-align: baseline;
}

[type="number"]::-webkit-inner-spin-button,
[type="number"]::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  outline-offset: -2px;
  -webkit-appearance: none;
}

[type="search"]::-webkit-search-cancel-button,
[type="search"]::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  font: inherit;
  -webkit-appearance: button;
}

output {
  display: inline-block;
}

summary {
  display: list-item;
}

template {
  display: none;
}

[hidden] {
  display: none !important;
}

@font-face {
  font-family: 'Vegur';
  src: url("../../src/fonts/Vegur-Regular-webfont.woff") format("woff");
  font-style: normal;
}

@font-face {
  font-family: 'VegurBold';
  src: url("../../src/fonts/Vegur-Bold-webfont.woff");
  font-weight: bold;
}

body {
  font-family: Vegur;
}

.txtcenter {
  text-align: center;
}

.flex {
  display: flex;
}

.flex.center {
  justify-content: center;
  align-items: center;
}

.flex.col {
  flex-direction: column;
}

.flex.spaceb {
  justify-content: space-between;
}

.wrap {
  width: 85%;
  margin: 0 auto;
}

img {
  width: 100%;
}

a {
  color: black;
}

.row {
  margin-bottom: 25px;
}

div.news {
  justify-content: space-between;
  align-items: center;
  font-family: VegurBold;
  margin-bottom: 20px;
}

div.news div:first-of-type {
  width: 40%;
}

div.news div:last-of-type {
  width: 50%;
}

div.news p.texte_actu {
  font-family: VegurBold;
  font-size: 1.15rem;
}

div.cadre_jaune_menu {
  width: 50%;
  height: 100%;
  background-color: #f0ce3c;
}

div.cadre_vert_menu {
  width: 50%;
  height: 100%;
  background-color: #86b438;
}

div.cadre_vert {
  width: 50%;
  height: 50px;
  background-color: #86b438;
}

div.cadre_jaune {
  width: 50%;
  height: 50px;
  background-color: #f0ce3c;
}

div.cadre_vert_full {
  width: 100%;
  height: 50px;
  background-color: #86b438;
}

header {
  position: relative;
  color: white;
}

header * {
  z-index: 2;
}

div.filtre_vert {
  background-color: #4a6b2d;
  position: absolute;
  z-index: 1;
  width: 100%;
  height: 100%;
  top: 0;
  bottom: 0;
}

div.menu {
  color: white;
  justify-content: space-between;
  padding: 20px;
}

div.menu a {
  color: white;
}

div.navbar {
  color: black;
  font-size: 0.75rem;
  justify-content: space-between;
  align-items: center;
  height: 75px;
}

div.navbar div.img_container {
  height: 100%;
}

div.navbar img {
  height: 100%;
}

div.news_container {
  flex-direction: column;
  align-items: center;
  margin-bottom: 75px;
}

@media all and (min-width: 1024px) {
  header {
    position: absolute;
    top: 0;
    width: 100%;
  }
  div.filtre_vert {
    opacity: 0.5;
    filter: contrast(200%) saturate(200%);
    background: #4a6b2d;
    background: linear-gradient(90deg, rgba(74, 107, 45, 0) 0%, #4a6b2d 40%, #4a6b2d 50%, #4a6b2d 60%, rgba(74, 107, 45, 0) 100%);
  }
  img.bgmobile_photo {
    z-index: -1;
  }
}
